import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import Jop from "src/components/Jop/Jop";
import styled from "styled-components";

export interface CareerProps {}

const Content = styled.div`
    padding: 20px 14% 50px 14%;

    & > .title {
        font-size: 24px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.61;
        -webkit-letter-spacing: normal;
        -moz-letter-spacing: normal;
        -ms-letter-spacing: normal;
        letter-spacing: normal;
        color: #000000;
        margin-bottom: 20px;
    }
`;

export default class Career extends React.Component<CareerProps, any> {
    public render() {
        return (
            <>
                <Navigation list={[{ name: "홈", link: "/" }, { name: "채용", link: "/career/" }]} />
                <Content>
                    <div className="title">진행중 공고</div>
                    <Jop />
                </Content>
            </>
        );
    }
}



